import { useState } from "react";
import {TiDelete } from "react-icons/ti";

function FormField({fieldId, fieldSno, date, time, tollGateName, amount, handleDeleteFormField, handleInputFieldChange}) {
    const [tempFormField, setTempFormField] = useState({
        id:fieldId,
        date:date,
        time:time,
        tollName:tollGateName,
        amount:amount
    })
    const [selectVal, setSelectVal] = useState("")

    function onDelete(){
        handleDeleteFormField(fieldId)
    }

    function handleInputChange(tempdata){
        handleInputFieldChange(fieldId, tempdata)
    }

    const othersField = selectVal === "Others" ? <input type="text" onChange={(e)=>{
        setTempFormField({...tempFormField,tollName: e.target.value});
        handleInputChange({...tempFormField,tollName:e.target.value});}}/> : "";

    return (
        <div className="form-field">
            <div className="field-sno">{fieldSno}</div>
            <div className="field-input-date">
                <input type="date" name="date" id="date" value={tempFormField.date} onChange={(e)=>{
                    setTempFormField({...tempFormField,date:e.target.value});
                    handleInputChange({...tempFormField,date:e.target.value});
                }} required/>
            </div>
            <div className="field-input-time">
                <input type="time" name="time" id="time" value={tempFormField.time} onChange={(e)=>{
                    setTempFormField({...tempFormField,time:e.target.value});
                    handleInputChange({...tempFormField,time:e.target.value});
                }} required/>
            </div>
            <div className="field-input-toll-name">
                <select name="toll-name" id="toll-name" value={selectVal==="Others"? selectVal : tempFormField.tollName} onChange={(e)=>{
                    setTempFormField({...tempFormField,tollName:e.target.value});
                    handleInputChange({...tempFormField,tollName:e.target.value});
                    setSelectVal(e.target.value);
                }} required>
                    <option value="Budhanam">Budhanam</option>
                    <option value="Chennasamaduram">Chennasamaduram</option>
                    <option value="Chinnamullaivoyal">Chinnamullaivoyal</option>
                    <option value="ITEL">ITEL</option>
                    <option value="Mahasamudram">Mahasamudram</option>
                    <option value="Mathur">Mathur</option>
                    <option value="Nallur">Nallur</option>
                    <option value="Nellore">Nellore</option>
                    <option value="Nemili">Nemili</option>
                    <option value="OMR">OMR</option>
                    <option value="Pallikonda">Pallikonda</option>
                    <option value="Paranur">Paranur</option>
                    <option value="Pattarai">Pattarai</option>
                    <option value="Pattaraiperumbudur">Pattaraiperumbudur</option>
                    <option value="Perungudi">Perungudi</option>
                    <option value="Porur">Porur</option>
                    <option value="S.V puram">S.V puram</option>
                    <option value="SriPerumbadur">SriPerumbadur</option>
                    <option value="Surapattu">Surapattu</option>
                    <option value="Uthandi">Uthandi</option>
                    <option value="Vanagaram">Vanagaram</option>
                    <option value="Others">Others</option>
                </select>
                {othersField}
            </div>
            <div className="field-input-amount">
                <input type="text" name="amount" id="amount" value={tempFormField.amount} onChange={(e)=>{
                    setTempFormField({...tempFormField,amount:e.target.value});
                    handleInputChange({...tempFormField,amount:e.target.value});
                }} required/>
            </div>
            <div className="delete-form-field">
                <button className="delete" onClick={onDelete}><TiDelete /></button>
            </div>
        </div>
    )
}

export default FormField;