import FormField from "./FormField";
import { useState } from "react";
import * as xlsx from "xlsx";
import {saveAs} from "file-saver";

function Form() {
    const [nextId, setNextId] = useState(2);
    const [truckNo, setTruckNo] = useState('');
    const [month, setMonth] = useState(1)
    const [year, setYear] = useState('');
    const [file, setFile] = useState(null);
    const [formData, setFormData] = useState([])

    let i = 0
    const formFieldList = formData.map((formField) => {
        i=i+1;
        return <FormField key={formField.id} fieldId={formField.id} fieldSno={i} date={formField.date} time={formField.time} tollGateName={formField.tollName} amount={formField.amount} handleDeleteFormField={handleDeleteField} handleInputFieldChange={handleInputChange}/>
    })

    function handelAddFormField(){
        setFormData([...formData,
        {
            id:nextId,
            date:"",
            time:"",
            tollName:"Mathur",
            amount:""
        }])
        setNextId(nextId+1)
    }

    function handleDeleteField(id){
        const newFormData = formData.filter((formField)=>{
            return formField.id!==id
        });
        setFormData([...newFormData]);
    }

    function handleInputChange(id, fieldData){        
        const newFormData = formData.map((formField)=>{
            if(formField.id===id){
                return fieldData
            }
            else{
                return formField
            }
        })

        setFormData([...newFormData])
    }

    function handleSubmit(){
        let isValidData = true;

        formData.forEach((formField) => {
            Object.keys(formField).forEach(formFieldKey=>{
                if(formField[formFieldKey]===""){
                    isValidData = false;
                }
            })
        })
        if(!(truckNo&&year&&month)){
            isValidData = false;
        }
        if(isValidData){
            const worksheet = xlsx.utils.json_to_sheet(formData);
            const workbook = xlsx.utils.book_new()
            xlsx.utils.book_append_sheet(workbook, worksheet, `${year}_${month}_${truckNo}`);

            const excelBuffer = xlsx.write(workbook, {bookType: 'xlsx', type: 'array'});
            const blob = new Blob([excelBuffer], {type:'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet;charset-UTF-8'});

            saveAs(blob, `${year}_${month}_${truckNo}.xlsx` );
        }
        else{
            alert("Please Fill Out all fields!!!");
        }
    }

    function handleExtractData(){
        if(file){
            const reader = new FileReader();
            reader.onload = (e)=>{
                const data = e.target.result;
                const workbook  = xlsx.read(data, {type:"binary"});
                const sheetName = workbook.SheetNames[0];
                const arr = sheetName.split("_");
                if(arr.length===3){
                    setYear(arr[0]);
                    setMonth(arr[1]);
                    setTruckNo(arr[2]);
                }
                const worksheet = workbook.Sheets[sheetName];
                const json = xlsx.utils.sheet_to_json(worksheet);
                setFormData(json);
                setNextId(json.length+1);
            };
            reader.readAsBinaryString(file);
        }
    }

    return (
        <>
        <div className="file-picker-input-container">
            <div className="file-picker-input">
                <input type="file" accept=".xlsx" onChange={(e)=>setFile(e.target.files[0])} />
                <button className="extract-file-data" onClick={handleExtractData}>Extract Data</button>
            </div>  
        </div>
        <form className="form-container">
            <div className="form-container-head">
                <div className="truck-no">
                    <label htmlFor="truck-no">
                        Truck No
                    </label>
                    <input type="text" name="truck-no" id="truck-no" maxLength={10} value={truckNo} onChange={(e)=>{setTruckNo(e.target.value)}} required/>
                </div>
                <div className="period">
                    <div className="month">
                        <label htmlFor="period-month">
                           Claim Month
                        </label>
                        <select name="month" id="month" value={month} onChange={(e)=>{
                            setMonth(e.target.value);
                        }}>
                            <option value="1">1</option>
                            <option value="2">2</option>
                            <option value="3">3</option>
                            <option value="4">4</option>
                            <option value="5">5</option>
                            <option value="6">6</option>
                            <option value="7">7</option>
                            <option value="8">8</option>
                            <option value="9">9</option>
                            <option value="10">10</option>
                            <option value="11">11</option>
                            <option value="12">12</option>
                        </select>
                    </div>
                    <div className="year">
                        <label htmlFor="period-year">
                           Claim Year
                        </label>
                        <input type="text" name="period-year" id="period-year" maxLength={4} value={year} onChange={(e)=>{
                            setYear(e.target.value);
                        }} required/>
                    </div>
                </div>
            </div>
            <div className="transactions-list">
                <div className="transactions-list-header">
                    <div className="field-sno">S.No</div>
                    <div className="field-input-date">Date</div>
                    <div className="field-input-time">Time</div>
                    <div className="field-input-toll-name">Toll Gate Name</div>
                    <div className="field-input-amount">Amount</div>
                    <div className="delete-form-field">Delete</div>
                </div>
                {formFieldList}
            </div>
            <div className="form-container-foot">
                <button className="add-form-field" onClick={handelAddFormField}>Add Line</button>
                <button type="submit" className="submit-form" onClick={(e)=>{
                    e.preventDefault();
                    handleSubmit();
                }}>Save As Excel</button>
            </div>
        </form>
        </>
    )
}

export default Form;